.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


#slider-form {
  text-align: right;
  margin-top: 5%;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  input[type='range'] {
    overflow: hidden;
    width: 225px;
    margin: 20px;
    -webkit-appearance: none;
    background-color: #d4d4d0;
  }

  input[type='range']::-webkit-slider-runnable-track {
    height: 12px;
    -webkit-appearance: none;
    color: #723ee7;
    margin-top: -1px;
  }

  input[type='range']::-webkit-slider-thumb {
    width: 10px;
    -webkit-appearance: none;
    height: 12px;
    cursor: ew-resize;
    background: #282727;
    box-shadow: -225px 0 0 225px #723ee7;
  }
}

#time-slider {
  width: 48%;
  float: right;
  margin-right: 20%;
}

#play-button {
  margin-top: 20px;
  height: 50px;
  width: 20%;
  font-size: 20px;
  float: bottom;
  margin-left: 40%;
  margin-right: 40%;
}

#bottom-div {
  float: bottom;
  width: 100%;
  margin-top: 100px;
}

#time-output {
  text-align: left;
  float: left;
  margin-left: 20%;
  margin-top: 1.75%;
  margin-right: 2%;
  width: 5%;
}

#tempo-output {
  text-align: right;
}

#tempo-slider {
  margin-top: 75px;
}

#time-slider-and-output-div {
  height: 50px;
}

